      let base = {
        show: 1,
        required: 1,
        uuid: this.get_uuid(),
        uuidme: this.get_uuid(),
        mockValue: ''
      }
      switch (type) {
        case 1 :
        case 2 :
        case 3 :
        case 4 :
        case 7 :
          return Object.assign({
            type: 4,
            name: 'number',
          }, base);
        case 5 :
          return Object.assign({
            type: 5,
            children: [
              {
                show: 1,
                type: 4,
                mockValue: "",
                keyIsNull: true,
                uuid: "" + this.get_uuid(),
                uuidme: "" + this.get_uuid(),
                required: 1
              }
            ],
          }, base);
        case 6 :
        case 8 :
          return Object.assign({
            name: 'name',
            type: 6,
            children: [
              {
                name: "key1",
                show: 1,
                type: 4,
                mockValue: "",
                uuid: "" + this.get_uuid(),
                uuidme: "" + this.get_uuid(),
                required: 1
              }
            ],
          }, base);
        case 9 :
          return Object.assign({
            type: 9,
            children: [
              {
                show: 1,
                mockValue: "",
                type: 4,
                uuid: "" + this.get_uuid(),
                uuidme: "" + this.get_uuid(),
                required: 1
              }
            ],
          }, base);
      }
    },
    get_uuid() {
      let s = [];
      let hexDigits = "0123456789abcdef";
      for (var i = 0; i < 36; i++) {
        s[i] = hexDigits.substr(Math.floor(Math.random() * 0x10), 1);
      }
      s[14] = "4"; // bits 12-15 of the time_hi_and_version field to 0010
      s[19] = hexDigits.substr((s[19] & 0x3) | 0x8, 1); // bits 6-7 of the clock_seq_hi_and_reserved to 01
      s[8] = s[13] = s[18] = s[23] = "";

      return s.join("");
    },
    test(data, selectType) {
      let choose = {
        uuidme: this.get_uuid(),
        show: 1,
        required: 1,
        mockValue: ''
      }
      let addData = {}
      if (selectType) {
        addData = this.getInsertTemplate(selectType)
      } else addData = choose
      let that = this
      // this.objectIn(addData,function (item) {
      //   item.uuidme = that.get_uuid()
      // })
      data.push(addData)

      console.log("插上数据的对象UUIDME:", data.uuidme)
      console.log("插上数据的对象UUIDME:", data)

      //强制重绘
      this.$forceUpdate()
      this.unitShow = false;
      this.$nextTick(() => {
        this.unitShow = true;
      })
    }
    ,
    set_recursion(data, id, type, selectType) {   //递归修改数据
      //0新增 1删除
      let that = this
      console.log(type)
      console.log(selectType)
      console.log(data)
      data.forEach((item, index) => {
            if (item.uuidme == id) {
              if (type == 0) {
                if (data instanceof Array) {
                  that.test(data, selectType)
                } else {
                }
              } else {
                data.splice(index, 1)
                //强制重绘
                this.$forceUpdate()
                this.unitShow = false;
                this.$nextTick(() => {
                  this.unitShow = true;
                })
              }
            } else {
              if (item.children && item.children.length) {
                that.set_recursion(item.children, id, type, selectType)
              }
            }
          }
      )

    },
    // searchData(childId){
    //     if(o.nodes != undefined){
    //         for(n in o.nodes){
    //             o.nodes[n].parent = o;
    //             this.setParent(o.nodes[n]);
    //         }
    //     }
    // }
  }
}
</script>

